import React, { Component } from "react"
import "./contact.css"
import { FaEnvelope, FaPhone } from "react-icons/fa"

class Contact extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: "",
            message: "",
            data: null,
            btn: "Wyślij",
            emailstate: "0.1rem solid #999",
            emailval: "",
        }
    }

    onChange = e => {
        this.setState({
            email: e.target.value,
        })
    }
    //zapisuje value z inputa jako message
    onChange2 = e => {
        this.setState({
            message: e.target.value,
        })
    }
    //wysyłam do firebase obiekt który ma w sobie name z inputa i message z textarea
    send = e => {
        e.preventDefault()

        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

        if (re.test(String(this.state.email).toLowerCase())) {
            var template_params = {
                emialK: this.state.email,
                message: this.state.message,
            }

            var service_id = "default_service"
            var template_id = "qwe"
            // emailjs.send(
            //     service_id,
            //     template_id,
            //     template_params,
            //     "user_9gdw09JxKNJT5GlaOOklB"
            // )

            this.setState({
                email: "",
                message: "",
                emailstate: "0.1rem solid #999",
                emailval: "",
                btn: "Dziękujemy za wiadomość",
            })
            setTimeout(() => {
                this.setState({
                    btn: "Wyślij",
                })
            }, 3000)
        } else {
            this.setState({
                emailstate: "0.1rem solid #c91630",
                emailval: "Nieprawidłowy adres e-mail",
            })
        }
    }
    render() {
        return (
            <div name="contact" className="section_container">
                <div className="contact">
                    <div className="contact_text">
                        <h4 className="section_heading">
                            Zapraszamy do kontaktu
                        </h4>
                        <p>
                            Odpowiemy na każde Państwa pytanie. Nasze usługi
                            wykonujemy na najwyższym poziomie, dlatego
                            proponujemy spotkanie w Państwa firmie celem
                            ustalenia zakresu obowiązków. Dzięki temu będziemy
                            mogli indywidualnie i z korzyścią dla Państwa
                            przygotować ofertę cenową.
                        </p>
                        <p>
                            Częstotliwość i ostateczny zakres prac ustalamy
                            indywidualnie, dostosowujemy je do wymagań klientów
                            oraz specyfiki biura lub hali.
                        </p>
                        <div className="contact_details">
                            <div className="email">
                                <a href="mailto:office@smartoakproject.com">
                                    <FaEnvelope />
                                    office@smartoakproject.com
                                </a>
                            </div>
                            <div className="phone">
                                <a href="tel:514-438-369">
                                    <FaPhone />
                                    514-438-369
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="form">
                        <form>
                            <label className="email_label" htmlFor="email-form">
                                {this.state.emailval}
                            </label>
                            <input
                                id="email-from"
                                className="user_email"
                                type="email"
                                placeholder="Twój e-mail"
                                value={this.state.email}
                                onChange={this.onChange}
                                style={{ border: this.state.emailstate }}
                            />
                            <div className="user_questions">
                                <div>
                                    <textarea
                                        value={this.state.message}
                                        onChange={this.onChange2}
                                        placeholder="Jak możemy pomóc?"
                                    />
                                </div>
                            </div>
                            <input
                                type="submit"
                                className="sent"
                                value={this.state.btn}
                                onClick={this.send}
                            />
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Contact
